<template>
    <div class="single-page-con">
      <div class="cangkuSeachBox">
        <Row>
          <Col span="4" v-if="btnFlag">
            <div class="cangkuSeach">
              <span>仓库编码</span>
              <Select v-model="params.storeCode" clearable style="width: 60%" filterable @on-change="sels(params)">
                <Option v-for="(item,index) in cityList" :value="item.storeCode" :key="index">{{ item.storeName }}</Option>
              </Select>
            </div>
          </Col>
          <Col span="4" v-if="btnFlag">
            <div class="cangkuSeach">
              <span>商品编码</span>
              <Input v-model="params.productCode" placeholder="商品名称..." style="width: 60%"></Input>
            </div>
          </Col>
          <Col span="4">
            <div class="cangkuSeach">
              <span>商品名称</span>
              <Input v-model="params.productName" placeholder="商品名称..." style="width: 60%"></Input>
            </div>
          </Col>
          <Col span="8" >
            <div class="cangkuSeach">
              <Button type="primary" @click="changelist">高级过滤</Button>
            </div>
          </Col>
        </Row>
        <Row style="margin-top: 20px;">
          <Col span="6">
            <Button type="ghost" shape="circle" icon="ios-search" @click="init(params,'edit')">查询商品</Button>
            <Button type="primary" icon="plus-round" style="margin-left: 30px" @click="newShangpin">新建商品</Button>
          </Col>
        </Row>
      </div>
      <div class="cangkuList">
        <h1 class="CklistTitle">商品列表</h1>
        <Table stripe border :loading="loading" :columns="columns7" :data="data6" height="500" ></Table>
      </div>
      <div class="cangkuPages">
        <Page :total="total"  show-sizer :page-size="params.size" :current="params.index"  @on-change="changePage" @on-page-size-change="changePageSize"></Page>
      </div>
      <Modal
        v-model="modal1"
        title="修改商品"
        @on-ok="ok(paramsEdit)"
        @on-cancel="cancel">
        <ul class="SPcont">
          <li>
            <Row>
              <Col span="24">
            <span class="intit">
              仓库编号
            </span>
                <Select v-model="paramsEdit.storeCode" clearable style="width: 60%" @on-change="sels(paramsEdit)">
                  <Option v-for="(item,index) in cityList" :value="item.storeCode" :key="index">{{ item.storeCode }}</Option>
                </Select>
              </Col>
            </Row>
          </li>
          <li>
            <Row>
              <Col span="24">
            <span class="intit">
              商品编号
            </span>
                <Input v-model="paramsEdit.productCode" placeholder="请输入商品编号..." style="width: 60%"></Input>
              </Col>
            </Row>
          </li>
          <li>
            <Row>
              <Col span="24">
            <span class="intit">
              商品名称
            </span>
                <Input v-model="paramsEdit.productName" placeholder="请输入商品名称..." style="width: 60%"></Input>
              </Col>
            </Row>
          </li>
          <li>
            <Row>
              <Col span="24">
            <span class="intit">
              库存个数
            </span>
                <Input v-model.trim="paramsEdit.inventory" @on-blur="reg(paramsEdit.inventory)" placeholder="请输入库存个数..." style="width: 60%"></Input>
              </Col>
            </Row>
          </li>
        </ul>
      </Modal>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'cangku',
  data () {
    return {
      btnFlag: false,
      loading: true,
      modal1: false,
      cityList: [],
      params: {
        index: 1,
        size: 20,
        productCode: '',
        productName: '',
        storeCode: '',
        storeName: ''
      },
      paramsEdit: {},
      paramsEditDatas: {},
      columns7: [
        {
          title: '仓库编号',
          key: 'storeCode',
          render: (h, params) => {
            return h('div', [
              h('Icon', {
                props: {
                  type: 'person'
                }
              }),
              h('strong', params.row.storeCode)
            ])
          }
        },
        {
          title: '仓库名称',
          key: 'storeName'
        },
        {
          title: '商品编号',
          key: 'productCode'
        },
        {
          title: '商品名称',
          key: 'productName'
        },
        {
          title: '库存个数',
          key: 'inventory'
        },
        {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.edit(params.index)
                  }
                }
              }, '修改'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.remove(params.index)
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data6: [
      ],
      total: 0,
      index: 0
    }
  },
  methods: {
    edit (index) { // 修改
      this.modal1 = true
      if (this.modal1) {
        this.cangkulist()
        this.paramsEdit = Object.assign({}, this.data6[index])
        // JSON.parse(JSON.stringify(this.data6[index]));
        this.paramsEditDatas = this.data6[index]
        this.index = index
      }
    },
    remove (index) {
      var scope = this
      this.$Modal.confirm({
        title: '删除商品',
        closable: true,
        content: `商品名称：${this.data6[index].productName}<br>仓库名称：${this.data6[index].storeName}`,
        onOk: () => {
          axios.delete(scope.baseUrl+'/product/' + this.data6[index].id).then(function (res) {
            if (res.data.suc) {
              scope.$Message.success({
                content: res.data.msg,
                duration: 3
              })
              scope.data6.splice(index, 1)
            } else {
              scope.$Message.error({
                content: res.data.msg,
                duration: 3
              })
            }
          })
        }
      })
    },
    changelist () { // 高级过滤
      this.btnFlag = !this.btnFlag
      if (!this.btnFlag) {
        this.params.productCode = ''
        this.params.storeCode = ''
      } else {
        this.cangkulist()
      }
    },
    newShangpin () {
      this.$router.push({name: 'newshangpin'})
    },
    init (params,flag) {
      if (flag == 'edit' ) {
        this.params.index = 1;
        this.params.size = 20;
      }
      const scope = this
      scope.loading = true
      axios.get(scope.baseUrl+'/product', {
        params: params
      })
        .then(function (response) {
          scope.loading = false
          if (response.data.suc) {
            scope.data6 = response.data.obj
            scope.total = response.data.total
          } else {
            scope.$Message.warning(response.data.msg)
            scope.data6 = response.data.obj
          }
        })
        .catch(function (error) {
          scope.loading = false
          console.log(error)
        })
    },
    changePage (index) {
      this.params.index = index
      this.init(this.params)
    },
    changePageSize (size) {
      this.params.size = size
      this.init(this.params)
    },
    ok (paramsEdit) {
      var scope = this
      axios.put(scope.baseUrl + '/product', paramsEdit)
        .then(function (res) {
          if (res.data.suc) {
            scope.$Message.info({
              content: res.data.msg,
              duration: 3
            })
            scope.init(scope.params)
          } else {
            scope.$Message.error('修改失败,'+  res.data.msg)
          }
        })
        .catch(function (error) {
          scope.$Message.error({
            content: error,
            duration: 3
          })
        })
    },
    cancel () {
      this.$Message.info('取消修改')
    },
    cangkulist () {
      var scope = this
      axios.get(scope.baseUrl + '/store')
        .then(function (response) {
          if (response.data.suc) {
            scope.cityList = response.data.obj
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    sels (v) {

      for (var i = 0; i < this.cityList.length; i++) {
        if (this.cityList[i].storeName == v.storeName  ) {
          if( v.storeCode = undefined) {
            if (this.cityList[i].storeCode == v.storeCode) {
              v.storeName = this.cityList[i].storeName
            }
          } else {
            this.cityList[i].storeName = ''
          }

        }
      }
    },
    reg (num) {
      const reg = new RegExp('^[1-9]\\d*|0$')
      if (num != '') {
        if (reg.test(num)) {
          if (parseInt(num) > 99999) {
            this.$Message.warning({
              content: '请输入0~99999的数值',
              duration: 3
            })
          }
        } else {
          this.$Message.warning({
            content: '请输入数字',
            duration: 3
          })
        }
      }
    }
  },
  computed: {
    datas () {
      return this.data6[this.index] = this.paramsEdit
    }

  },
  created () {
    this.init(this.params)
  }
}
</script>

