  .single-page-con {
    margin: 20px 20px 20px 20px;
    overflow: auto;
  }
  .cangkuSeachBox {
    height: auto;
    box-shadow: 0px 2px 4px #ccc ;
    padding: 20px 20px 20px 20px;
  }
  .cangkuSeach{
    font-size: 14px;
    font-weight: bold;
      &>span{
        margin-right:20px;
      }
  }
  .cangkuList{
    width: auto;
    height: auto;
    margin-top: 30px;
    box-shadow: 0px 2px 4px #ccc ;
      &>.CklistTitle{
        padding: 10px 0 10px 10px;
        overflow: hidden;
    }
  }
  .cangkuPages {
    float: right;
    margin-top: 20px;
  }
  ul,li{
    list-style: none;
  }
  .intit {
    padding-right: 20px;
